* {
    box-sizing: border-box;
    font-family: sans-serif;
    margin: 0;
    padding: 0;
    text-align: center;
    color: #001C30;
}

ul {
    list-style: none;
}

header {
    padding: 2.5vh 0;
    position: fixed;
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    background-color: #001C30;
}

header h1 {
    color: #64CCC5;
    text-transform: uppercase;
}

.add__product__button {
    background-color: #DAFFFB;
    border-radius: 4px;
    border-style: none;
    box-sizing: border-box;
    color: #001C30;
    cursor: pointer;
    display: inline-block;
    font-size: 16px;
    font-weight: 700;
    line-height: 1.5;
    margin: 0;
    max-width: none;
    min-height: 44px;
    min-width: 10px;
    outline: none;
    overflow: hidden;
    padding: 9px 20px 8px;
    position: relative;
    text-align: center;
    user-select: none;
    touch-action: manipulation;
    width: auto;
}

.add__product__button:hover,
.add__product__button:focus {
    opacity: .75;
}

.items__list {
    display: flex;
    justify-content: space-evenly;
    flex-direction: column;
    padding-top: 15vh;
    padding-bottom: 0.25rem;
    border-bottom: 2px solid #001C30;
    font-weight: 600;
}

.items__list__row,
.categories__list__row {
    font-size: 1.4rem;
    margin: 0 auto;
    width: 80%;
    display: flex;
    justify-content: space-evenly;
    flex-direction: row;
    align-items: center;
}

.items__list__row {
    margin-top: 4vh;
}

.items__list__row__position {
    width: 25%;
}

.remove__item {
    background: #FF4742;
    border: 1px solid #FF4742;
    border-radius: 6px;
    box-shadow: rgba(0, 0, 0, 0.1) 1px 2px 4px;
    box-sizing: border-box;
    color: #FFFFFF;
    cursor: pointer;
    display: inline-block;
    font-family: nunito, roboto, proxima-nova, "proxima nova", sans-serif;
    font-size: 14px;
    font-weight: 700;
    line-height: 14px;
    min-height: 36px;
    outline: none;
    padding: 7px 12px;
    text-align: center;
    text-rendering: geometricprecision;
    text-transform: none;
    user-select: none;
    touch-action: manipulation;
    vertical-align: middle;
    transition: .2s;
}

.remove__item:hover,
.remove__item:active {
    background-color: transparent;
    background-position: 0 0;
    color: #FF4742;
}

.remove__item:active {
    opacity: .5;
    transition: 0;
}

.days__until {
    font-size: 0.88rem;
}

.days__until span {
    font-weight: bold;
}


/*  MODAL  */

.modal {
    display: none;
    position: fixed;
    z-index: 5;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0, 0, 0, 0.7);
}

.modal__content {
    background-color: #001C30;
    margin: 15% auto;
    /* 15% from the top and centered */
    padding: 25px;
    border: none;
    width: 80%;
    /* Could be more or less, depending on screen size */
}

.modal__content .close__modal {
    color: #dcdcdc;
    float: right;
    font-size: 28px;
    font-weight: bold;
}

.close__modal:hover,
.close__modal:focus {
    color: red;
    text-decoration: none;
    cursor: pointer;
}

.modal.show__modal {
    display: block;
}

.modal__row {
    display: flex;
    justify-content: space-evenly;
    width: 80%;
    margin: 5% auto;
}

.modal label {
    color: #64CCC5;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.modal label input {
    text-align: left;
    outline: none;
}

.modal button {
    background-color: #DAFFFB;
    border-radius: 4px;
    border-style: none;
    box-sizing: border-box;
    color: #001C30;
    cursor: pointer;
    display: inline-block;
    font-size: 16px;
    font-weight: 700;
    line-height: 1.5;
    margin: 0 auto;
    max-width: none;
    min-height: 44px;
    min-width: 10px;
    outline: none;
    overflow: hidden;
    padding: 9px 20px 8px;
    position: relative;
    text-align: center;
    user-select: none;
    touch-action: manipulation;
    width: auto;
}